<template>
  <div>
    <form id="main_form" action="" method="POST" @submit.prevent="checkSubmit()">
      <div style="margin:0px auto;" id="main_render">
        <div id="render_element" style="width:100%:">
          <div v-for="(item,index) in render_array" v-if="item.type!='submit'" v-html="item.html_render"></div>
        </div>
        <div id="render_element2" style="width:100%:">
          <div v-for="(item,index) in render_array" v-if="item.type=='submit'" v-html="item.html_render"></div>
        </div>
        <input type="hidden" name="subscriber_id" v-model="subscriber_id">
        <input type="hidden" name="profilling_id" v-model="profilling_id">
      </div>
    </form>
  </div>
</template>


<script>
  import itemText from './items/text';
  import itemTextLink from './items/textlink';
  import itemImages from './items/images';
  import itemImagesLink from './items/imageslink';
  import itemTextBox from './items/textbox';
  import itemSelectBox from './items/selectbox';
  import itemRadio from './items/radio';
  import itemTextBoxMd from './items/textbox-md';
  import itemCheckBox from './items/checkbox';
  import itemCheckBoxAgree from './items/checkbox-agree';
  import itemFWDPopup from './items/hellosoda_popup';
  import itemSubmit from './items/submit';
  
  import itemTextArea from './items/textarea';
  import itemDate from './items/select_date';
  import itemRadioBool from './items/radio_bool';
  import 'jquery-slimscroll';
  export default {
    data() {
      return {
        display_object: {},
        render_wallpaper_color: {},
        render_array: [],
        testx: "0",
        subscriber_id: 0,
        profilling_id: 0,
        _json_data: [],
        _json_data2: [],
        chk_agree: false
      }
    },
  
    created() {
      let _json = $("#hid_profilling").html();
      let _json2 = $("#hid_subdata").html();
      this._json_data = JSON.parse(_json);
      //เรียงข้อมูลตาม Seq ใหม่
      this._json_data.profilling_actions.sort(function(a, b) {
        return a.seq_no - b.seq_no;
      });
      this._json_data2 = JSON.parse(_json2);
      //$("#subscriber_id").val(_json_data.subscriber_id);
      this.subscriber_id = this._json_data.subscriber_id;
      this.profilling_id = this._json_data.profilling_id;
      //let _tempform = $("#main_form").prop("action");
      console.log(["_json_data", this._json_data]);
  
      //ทำ Auto Fill ถ้าใครเคยใส่ข้อมูลแล้วให้ กลับมาใช้ข้อมูลเดิมที่เคยกรอก
      this._json_data.profilling_actions.map((v, i) => {
        v.auto_fill = "";
      });
      if (this._json_data2.length > 0) {
        this._json_data.profilling_actions.map((v, i) => {
          if (v.field_id > 0) {
            let find_arr = this._json_data2.find((x) => {
              return x.field_id == v.field_id;
            });
            if (find_arr != undefined) {
              v.auto_fill = find_arr.value;
            }
          }
        });
      }
  
      //สร้าง data_set ให้ ข้อมูลจำพวก enum แต่ละ ตัว
      this._json_data.profilling_actions.map((v, i) => {
        if (v.id != "") {
          console.log(["v.field", v.field]);
          let _check_field_null = v.field === null;
          if ((v.field != null || _check_field_null == false)) { //Check Subscriber Element
            if (v.field.field_items.length > 0) { //Check Have enum_items
              let new_options = [];
  
              v.field.field_items.map((v, i) => {
                new_options.push({
                  id: v.id,
                  title: v.value
                });
              });
              v.data_set = new_options;
            } else {
              v.data_set = [];
            }
  
            if (v.field.type == "boolean") {
              v.data_set = [{
                id: 1,
                title: "Yes"
              }, {
                id: 0,
                title: "No"
              }];
            }
  
            if (v.type == "textbox") v.html_render = itemTextBox.textbox_template(v);
            else if (v.type == "selectbox") v.html_render = itemSelectBox.select_template(v);
            else if (v.type == "radio") v.html_render = itemRadio.radio_template(v);
            else if (v.type == "checkbox") v.html_render = itemCheckBox.chk_template(v);
  
            else if (v.type == "textarea") v.html_render = itemTextArea.textarea_template(v);
            else if (v.type == "date") v.html_render = itemDate.date_template(v);
            else if (v.type == "radio_bool") v.html_render = itemRadioBool.radio_bool_template(v);
          } else {
            if (v.type == "text") v.html_render = itemText.text_template(v);
            else if (v.type == "textlink") v.html_render = itemTextLink.textlink_template(v);
            else if (v.type == "submit") v.html_render = itemSubmit.submit_template(v);
            else if (v.type == "images") v.html_render = itemImages.img_template(v);
            else if (v.type == "images_link") v.html_render = itemImagesLink.imglink_template(v);
            else if (v.type == "checkbox_agree") v.html_render = itemCheckBoxAgree.chk_agree_template(v);
            else if (v.type == "fwd_popup") v.html_render = itemFWDPopup.hellosoda_popup_template(v);
  
          }
  
        }
      });
  
      this.render_array = this._json_data.profilling_actions;
    },
    mounted() {
      let _hostname = $('meta[name="hostname"]').attr('content');
      $("#main_form").prop("action", _hostname + "/activity");
  
      //ทำ Scorll Bar
      if ($('input[type="submit"]').length > 0) {
        let _submit_height = $('input[type="submit"]').css("height");
        _submit_height = _submit_height.split("px");
        let _window_height = $(window).height();
        $("#render_element").css("height", _window_height - _submit_height[0]);
        $('#render_element').slimScroll({
          height: $("#render_element").css("height")
        });
      }
  
      this.set_body_wallpaper_color(this._json_data.color_wallpaper);
  
      let _chk_arg = $(".chk_arg");
      if (_chk_arg.length > 0) {
        let is_checked = $(_chk_arg[0]).is(':checked');
        if (is_checked) {
          $('input[type="submit"]').prop('disabled', false);
        } else {
          $('input[type="submit"]').prop('disabled', true);
        }
        $(_chk_arg[0]).change(function() {
          if (this.checked) {
            $('input[type="submit"]').prop('disabled', false);
          } else {
            $('input[type="submit"]').prop('disabled', true);
          }
        });
      }
  
    },
    methods: {
      cssToObject(css) {
        let _arr = [];
        $.each(css, function(i2, v2) {
          let _css = "";
          var arx = ["width", "height", "font-size"];
          var n = arx.indexOf(v2.key);
          var check_px = v2.values.split("px");
          if (check_px.length == 2) {
            _css += v2.key + ":" + (parseInt(check_px[0]) * 1) + "px;";
          } else {
            _css += v2.key + ":" + (v2.values) + ";";
          }
          _arr.push(_css);
        });
        return _arr;
      },
      get_object_from_api() {
        var api_object_string = $("#api_values").html();
        $("#api_values").remove(); //TODO
        var api_object = JSON.parse(api_object_string);
        return api_object;
      },
      set_body_wallpaper_color(color) {
        $("body").css("background-color", this._json_data.page_color);
        $("#main_render").css("background-color", color);
  
  
        if (navigator.userAgent.match(/Android/i) ||
          navigator.userAgent.match(/webOS/i) ||
          navigator.userAgent.match(/iPhone/i) ||
          navigator.userAgent.match(/iPod/i) ||
          navigator.userAgent.match(/BlackBerry/i) ||
          navigator.userAgent.match(/Windows Phone/i)
        ) {
          $("#main_render").css("width", "100%");
        } else {
          $("#main_render").css("width", this._json_data.max_page_width);
        }
      },
      convert_css_and_setting(v) {
        var css_string = "{";
        $.each(v, function(i2, v2) {
          if (i2 > 0) {
            css_string += ",";
          }
          css_string += `"${v2.key}":"${v2.values.toString().replace(/"/g, '\\"')}"`
          //css_string += "\"" + v2.key + "\":\"" + v2.values + "\"";
        });
        css_string += "}";
        return JSON.parse(css_string);
      },
      checkSubmit() {
        let pass_submit = true;
        $.each($(".valid_textbox"), function() {
          let count_string = $(this).val().length;
          if (count_string == 0) {
            pass_submit = false;
          }
        });
        $.each($(".valid_selectbox"), function() {
          let check_select = $(this).val();
          if (check_select == "-1") {
            pass_submit = false;
          }
        });
        //valid_radio TODO: อย่าลืม Valid radio
  
        if (pass_submit) {
          document.getElementById("main_form").submit();
        } else {
          alert("กรุณากรอกข้อมูลให้ครบถ้วน");
          return false;
        }
      }
    }
  }
</script>





